@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 5.0625rem 1.5rem;
  font-family: var(--font-rubik);
  background: var(--background);
  color: var(--white-text);
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  body {
    padding: 15.8125rem 10.3125rem;
  }
}

main {
  max-width: 69.375rem;
  margin-bottom: 0.9375rem;
}

:root {
  --image-card: hsl(246, 80%, 60%);
  --works-card: hsl(15, 100%, 70%);
  --play-card: hsl(195, 74%, 62%);
  --study-card: hsl(348, 100%, 68%);
  --exercise-card: hsl(145, 58%, 55%);
  --social-card: hsl(263, 64%, 52%);
  --selfcare-card: hsl(43, 84%, 65%);
  --background: hsl(226, 43%, 10%);
  --main-card-color: hsl(235, 46%, 20%);
  --hover-card: hsl(236, 41%, 34%);
  --text-card-period: hsl(235, 45%, 61%);
  --text-pale-blue: hsl(236, 100%, 87%);
  --white-text: hsl(0, 0%, 100%);
}

:root {
  --font-rubik: "Rubik", sans-serif;
}

.box {
  --colors: var(--image-card), var(--works-card), var(--play-card), var(--study-card), var(--exercise-card), var(--social-card), var(--selfcare-card);
  --n: 7;
  --i: 0;
  background: linear-gradient(var(--colors)) no-repeat 0 calc(var(--i) * 100% / (var(--n) - 1))/100% calc(infinity * 1px);
}

.box::before {
  counter-reset: index var(--i);
  display: block;
}

.card {
  display: grid;
  width: 20.5rem;
  grid-template-columns: 20.5rem;
  gap: 1.5625rem;
}
@media (min-width: 71.875em) {
  .card {
    width: 15.9375rem;
    grid-template-columns: 15.9375rem 15.9375rem 15.9375rem 15.9375rem;
    gap: 1.875rem;
  }
}
.card__summary {
  color: var(--text-pale-blue);
  font-size: 0.625rem;
}
@media (min-width: 71.875em) {
  .card__summary {
    font-size: 0.75rem;
  }
}
.card__time {
  font-weight: 300;
  font-size: 1.5rem;
}
@media (min-width: 71.875em) {
  .card__time {
    font-size: 2.8125rem;
  }
}
.card__flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 1rem;
  align-items: center;
}
@media (min-width: 71.875em) {
  .card__flex {
    margin-bottom: 2.25rem;
  }
}
.card__activity-name img {
  cursor: pointer;
}
.card__activity-name {
  font-size: 0.75rem;
  font-weight: 500;
}
@media (min-width: 71.875em) {
  .card__activity-name {
    font-size: 0.875rem;
  }
}
.card__svg {
  height: 3.75rem;
  width: auto;
  position: relative;
  left: -0.9375rem;
  top: -0.3125rem;
  text-align: right;
  overflow: hidden;
}
.card__image {
  border-radius: 0.9375rem;
  height: 8.25rem;
}
@media (min-width: 71.875em) {
  .card__image {
    height: 22.125rem;
    padding: 2.1875rem;
  }
}
.card__image-name {
  color: var(--white-text);
  font-size: 1.125rem;
  font-weight: 300;
}
@media (min-width: 71.875em) {
  .card__image-name {
    font-size: 1.875rem;
  }
}
.card__activity {
  border-radius: 15px;
  overflow: hidden;
  height: 10rem;
}
@media (min-width: 71.875em) {
  .card__activity {
    height: 15.25rem;
  }
}
.card__activity-darkblue {
  padding: 2.1875rem 1.5625rem;
  height: 7.5rem;
  border-radius: 0.9375rem;
  background: var(--main-card-color);
  position: relative;
  top: -1.25rem;
}
@media (min-width: 71.875em) {
  .card__activity-darkblue {
    padding: 2rem;
    height: 12.4375rem;
    position: relative;
    top: -0.875rem;
  }
}
.card__activity-darkblue:hover {
  background: var(--hover-card);
  cursor: pointer;
}

.small-mod {
  padding-left: 1.5625rem;
  font-size: 0.75rem;
  color: var(--text-pale-blue);
}
@media (min-width: 71.875em) {
  .small-mod {
    padding-left: 0;
  }
}

.grid {
  background: var(--main-card-color);
  border-radius: 0.9375rem;
  height: 12.5rem;
}
@media (min-width: 71.875em) {
  .grid {
    grid-row: 1/3;
    height: 32.375rem;
  }
}

#photo {
  border: 2px solid var(--white-text);
  border-radius: 50%;
  height: 4.375rem;
}
@media (min-width: 71.875em) {
  #photo {
    display: block;
    height: 5.3125rem;
    margin-bottom: 1.875rem;
  }
}

.flex-small {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 71.875em) {
  .flex-small {
    display: block;
  }
}

.card__image-flex {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 6.25rem 0 1.875rem;
  justify-content: space-between;
}
@media (min-width: 71.875em) {
  .card__image-flex {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 2.1875rem;
  }
}

.name-grid {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 0.25rem;
  padding-left: 1.5625rem;
}
@media (min-width: 71.875em) {
  .name-grid {
    display: block;
    padding-left: 0;
  }
}

#radio1,
#radio2,
#radio3 {
  all: unset;
}

label[for=radio1]:hover,
label[for=radio2]:hover,
label[for=radio3]:hover {
  color: var(--white-text);
  cursor: pointer;
}

input[type=radio]:checked ~ label {
  color: var(--white-text);
  caret-color: transparent;
}

fieldset {
  border: none;
}

ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 1.25rem;
  list-style-type: none;
  font-size: 0.875rem;
  margin-left: -2.5rem;
  color: var(--text-pale-blue);
}
@media (min-width: 71.875em) {
  ul {
    margin-top: 2.5rem;
    display: flex;
    flex-direction: column;
    gap: 0.625rem;
  }
}

li {
  margin-left: -1.25rem;
}
@media (min-width: 71.875em) {
  li {
    margin-left: 1.25rem;
  }
}